
import { mapState, mapMutations } from 'vuex';
import XHeader from 'vux/src/components/x-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'

export default {
name: 'user',
data() {
return {

}
},
computed: {
...mapState({
deptList: state => state.common.dept.deptList,
vm: state => state.common.dept.vm
})
},
components: {
XHeader,
Popup,
PopupHeader,
Checklist
},
created() {
this.query();
},
methods: {
...mapMutations({
query: 'common/dept/getList',
}),
gotoForm(item) {
this.$store.commit('common/dept/clear');
if (item) {
this.$router.push({ path: "/system/dept/form", query: { deptId: item.id } });
} else {
this.$router.push({ path: "/system/dept/form" });
}
},
handleDelete(userId) {
const _this = this;
this.$vux.confirm.show({
title: "删除提示",
content: "确定要删除该条记录?",
onCancel() {
_this.$vux.toast.text('已取消删除', 'top');
},
onConfirm() {
_this.$store.commit({
type: 'common/dept/delete',
id: userId
})
}
})
}
}
}



